import React, { Component } from 'react';
import bgi from '../images/bgimg.jpg'
import { connect } from 'react-redux';
// import BodyField from './surveys/BodyField';
// import { reduxForm, Field } from 'redux-form';


class Landing extends Component {
    render() {
        return (
            <div style={{ backgroundImage: `url(${bgi})`, height: "100%", backgroundPosition: "center", backgroundRepeat: "no-repeat", backgroundSize: "cover" }}>
                <div style={{
                        flex: 1,
                        fontSize: 25,
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        fontFamily:"comicsans"
                    }}
                >
                    <b style={{margin: "0px 0px", fontSize: "30px"}}>what do I do?</b>
                </div >
                <div style={{
                        flex: 1,
                        fontSize: 25,
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        fontFamily:"comicsans"
                    }}
                >
                <p style={{marginBottom: "100px"}}>You can add credits and use them to send bulk emails in single click</p>
                </div>
                
                <div>
                <div className="row text-center">
                    <div className="col-lg-4 m-6 p-6" style={{marginLeft: 40}}>
                        <div className="card" style={{width: "18rem"}}>
                        <div className="card-body">
                            <h5 className="card-title"><b>Contact Us On</b></h5>
                            {/* <p className="card-text">Basic Plan</p> */}
                        </div>
                        <ul className="list-group list-group-flush ">
                            {/* <li className="list-group-item"><Field 
                                label = "Email Body" 
                                type = "text" 
                                name = "body" 
                                component  = {BodyField}
                            /></li> */}
                            <li className="list-group-item">Email: niteshgoud7366@gmail.com</li>
                            <li className="list-group-item">Send any suggestions / feedback / New Features to above email</li>
                        </ul>
                        <div className="card-body text-center">
                            <a href="/" className="btn btn-primary">Happy Hacking</a>
                        </div>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        );
            }
}


function mapStateToProps({ auth }) {
    return { auth };
}

export default connect(mapStateToProps)(Landing);

